:root {
  --pri-font:"Ubuntu", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(216, 93%, 44%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  /* width */
  /* Track */
  /* Handle */
  /* Handle on hover */
}
html ::-webkit-scrollbar {
  width: 5px !important;
}
html ::-webkit-scrollbar-track {
  background: #f1f1f1;
}
html ::-webkit-scrollbar-thumb {
  background: var(--blue) !important;
  border-radius: 2rem;
}
html ::-webkit-scrollbar-thumb:hover {
  background: var(--blue) !important;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.logo:hover {
  opacity: 1 !important;
}

nav a {
  transition: opacity 0.2s ease-in-out !important;
}
nav a:hover {
  opacity: 0.6;
}

.nav {
  width: min(50%, 12.5rem);
  outline: 1px solid rgb(234, 232, 232);
  transition: all 0.2s ease-in-out !important;
}
.nav li {
  transition: background-color 0.2s ease-in-out !important;
}

a {
  transition: opacity 0.2s ease-in-out !important;
}
a:hover {
  opacity: 0.6;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 0.8s ease-in-out;
}

/**
 * Extracted from: SweetAlert
 * Modified by: Istiak Tridip
 */
.success-checkmark {
  width: 80px;
  height: 115px;
  margin: 0 auto;
}
.success-checkmark .check-icon {
  width: 80px;
  height: 80px;
  position: relative;
  border-radius: 50%;
  box-sizing: content-box;
  border: 4px solid #4CAF50;
}
.success-checkmark .check-icon::before {
  top: 3px;
  left: -2px;
  width: 30px;
  transform-origin: 100% 50%;
  border-radius: 100px 0 0 100px;
}
.success-checkmark .check-icon::after {
  top: 0;
  left: 30px;
  width: 60px;
  transform-origin: 0 50%;
  border-radius: 0 100px 100px 0;
  animation: rotate-circle 4.25s ease-in;
}
.success-checkmark .check-icon::before, .success-checkmark .check-icon::after {
  content: "";
  height: 100px;
  position: absolute;
  background: #FFFFFF;
  transform: rotate(-45deg);
}
.success-checkmark .check-icon .icon-line {
  height: 5px;
  background-color: #4CAF50;
  display: block;
  border-radius: 2px;
  position: absolute;
  z-index: 10;
}
.success-checkmark .check-icon .icon-line.line-tip {
  top: 46px;
  left: 14px;
  width: 25px;
  transform: rotate(45deg);
  animation: icon-line-tip 0.75s;
}
.success-checkmark .check-icon .icon-line.line-long {
  top: 38px;
  right: 8px;
  width: 47px;
  transform: rotate(-45deg);
  animation: icon-line-long 0.75s;
}
.success-checkmark .check-icon .icon-circle {
  top: -4px;
  left: -4px;
  z-index: 10;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: absolute;
  box-sizing: content-box;
  border: 4px solid rgba(76, 175, 80, 0.5);
}
.success-checkmark .check-icon .icon-fix {
  top: 8px;
  width: 5px;
  left: 26px;
  z-index: 1;
  height: 85px;
  position: absolute;
  transform: rotate(-45deg);
  background-color: #FFFFFF;
}

@keyframes rotate-circle {
  0% {
    transform: rotate(-45deg);
  }
  5% {
    transform: rotate(-45deg);
  }
  12% {
    transform: rotate(-405deg);
  }
  100% {
    transform: rotate(-405deg);
  }
}
@keyframes icon-line-tip {
  0% {
    width: 0;
    left: 1px;
    top: 19px;
  }
  54% {
    width: 0;
    left: 1px;
    top: 19px;
  }
  70% {
    width: 50px;
    left: -8px;
    top: 37px;
  }
  84% {
    width: 17px;
    left: 21px;
    top: 48px;
  }
  100% {
    width: 25px;
    left: 14px;
    top: 45px;
  }
}
@keyframes icon-line-long {
  0% {
    width: 0;
    right: 46px;
    top: 54px;
  }
  65% {
    width: 0;
    right: 46px;
    top: 54px;
  }
  84% {
    width: 55px;
    right: 0px;
    top: 35px;
  }
  100% {
    width: 47px;
    right: 8px;
    top: 38px;
  }
}
.form {
  width: min(95%, 28.125rem);
}
@media (max-width: 63.9375em) {
  .form {
    margin: 0 auto;
  }
}
.form input, .form textarea {
  transition: outline-color 0.2s ease-in-out !important;
}
.form input:focus, .form textarea:focus {
  outline: 1px solid var(--blue);
}
.form input:not(:first-child), .form textarea:not(:first-child) {
  margin-top: 1.8rem !important;
}
.form input.err, .form textarea.err {
  border: 1px solid red;
}
.form button {
  transition: opacity 0.2s ease-in-out !important;
}
.form button:hover {
  opacity: 0.8;
}
.form button:disabled {
  opacity: 0.2;
  pointer-events: none;
}
.form i {
  opacity: 0.5;
}
.form #line {
  position: relative;
}
.form #line::before, .form #line::after {
  content: "";
  position: absolute;
  display: block;
  height: 0.6px;
  background-color: rgb(201, 197, 197);
  width: 45%;
}
.form #line::before {
  top: 50%;
  left: 0;
}
.form #line::after {
  top: 50%;
  right: 0;
}

.input {
  transition: outline-color 0.2s ease-in-out !important;
}

.categories__ctn {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  grid-auto-rows: 100px;
}
@media (max-width: 63.9375em) {
  .categories__ctn {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 39.937em) {
  .categories__ctn {
    grid-template-columns: repeat(1, 1fr);
  }
}
.categories__ctn .category {
  width: min(100%, 21.875rem);
}
@media (max-width: 63.9375em) {
  .categories__ctn .category {
    justify-self: center;
  }
}

.img-object {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

.img-object2 {
  width: 20px;
  height: 20px;
  border-radius: 100%;
}

.text-object {
  width: 60%;
  height: 20px;
  border-radius: 3px;
}

.text-object2 {
  width: 35%;
  height: 20px;
  border-radius: 3px;
}

.loading-state {
  animation: loading 0.7s linear infinite alternate;
  opacity: 0.8;
}

@keyframes loading {
  0% {
    background-color: hsl(200deg, 20%, 70%);
  }
  100% {
    background-color: hsl(200deg, 20%, 95%);
  }
}
.overlay {
  height: 100vh !important;
  width: 100vw !important;
  inset: 0;
  opacity: 0.3;
}

.modal {
  width: 90%;
  max-width: 28.125rem;
  height: auto;
  overflow: hidden;
  top: 50%;
  left: 50%;
  transform: translate(0%, -50%);
}
.modal input {
  transition: outline-color 0.2s ease-in-out !important;
}
.modal input:focus {
  outline: 1px solid var(--blue);
}
.modal button {
  transition: opacity 0.2s ease-in-out !important;
}
.modal button:hover {
  opacity: 0.7;
}

.vh {
  visibility: hidden;
}

.overlay,
.modal {
  display: none;
}

.overlay {
  opacity: 0.3 !important;
}

/* Keyframes for fading in and out */
@keyframes fadeIn {
  0% {
    opacity: 0;
    visibility: hidden;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
/* CSS for the element to be animated */
.hasFade {
  animation-duration: 0.3s;
  animation-fill-mode: both;
}

.fade-in {
  animation-name: fadeIn;
}

.fade-out {
  animation-name: fadeOut;
}

